/* Container */
.comingsoon-container {
  background-color: #000;
  color: #fff;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 32px;
  border-bottom: 1px solid white;
}

.logo {
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 1px;
}

.logo-highlight {
  color: #b3b3b3;
}
.logo-img {
  height: 50px;   
  width: 100px;
  object-fit: contain;
}

.menu-btn {
  background: none;
  border: none;
  color: #fff;
  font-size: 15px;
  letter-spacing: 0.5px;
  cursor: pointer;
}

.menu-icon {
  margin-left: 5px;
  font-size: 18px;
}

/* Main Content */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.rocket-img {
  width: 110px;
  margin-bottom: 14px;
}

.main-title {
  font-size: 32px;
  font-weight: bold;
  letter-spacing: 1px;
  margin-bottom: 1px;
}

.subtitle {
  color: white;
  font-size: 16px;
   margin-top: 0; 
}

/* Footer */
.footer {
  border-top: 1px solid white;
  padding: 20px 180px;
  font-size: 14px;
  color: white;
}

.footer-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 8px;
  border-bottom: 1px solid white;
}

.footer-links {
  display: flex;
  gap: 40px;
}

.footer-links a {
  color: white;
  text-decoration: none;
}

.footer-links a:hover {
  color: #fff;
}

/* Social Icons */
.social-icons {
  display: flex;
  gap: 10px;
  align-items: center;
}

.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  background: none;
  color: white;
  border: none;
  padding: 0;
  cursor: pointer;
}

.icon:hover {
  color: #ccc;
}

.youtube-icon,
.linkedin-icon,
.twitter-icon
{
  background: none !important;
  border-radius: 0;
}
.twitter-icon svg {
  color: white;
  stroke: white;
  stroke-width: 30px;
}
/* Instagram outlined with white background */

/* Footer bottom */
.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
}

.small-links {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.small-links a {
  color: white;
  text-decoration: none;
}

.small-links a:hover {
  color: #fff;
}

.small-links span {
  color: white;
}

.copyright {
  font-size: 13px;
  color: white;
}/* Menu Overlay (White Bar) */
.menu-overlay {
  position: fixed;
  top: 3%;
  left:5%;
  width: 86%; 
  height: 40px;
  background: #fff;
  color: #000;
  z-index: 2000;
  display: flex;
  align-items: center;
  padding: 12px 32px;
  border-radius: 6px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}


.menu-links {
  display: flex;
  gap: 28px;
  flex: 1;
  justify-content: center;
  margin-right: 50%;
}

.menu-links a {
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  color: #000;
}

.menu-links a:hover {
  color: #444;
}

/* Right Close Button */
.menu-close-btn {
  position: absolute;
  top: 14px;          /* distance from top */
  right: 20px;        /* distance from right */
  background: none;
  color: #000;        /* black cross */
  font-size: 24px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  z-index: 5000;
}


.menu-logo img {
 height: 50px;   
  width: 100px;
  object-fit: contain;
}

/* ========== Responsive Styles ========== */

/* Small phones (portrait, under 480px) */
@media (max-width: 480px) {
  .header {
    flex-direction: column;
    padding: 10px;
    text-align: center;
  }

  .logo {
    font-size: 22px;
  }

  .menu-btn {
    margin-top: 6px;
    font-size: 14px;
  }

  .rocket-img {
    width: 80px;
    margin-bottom: 10px;
  }

  .main-title {
    font-size: 22px;
  }

  .subtitle {
    font-size: 14px;
  }

  .footer {
    padding: 15px;
    text-align: center;
  }

  .footer-top {
    flex-direction: column;
    gap: 10px;
  }

  .footer-links {
    flex-wrap: wrap;
    gap: 15px;
    justify-content: center;
  }

  .social-icons {
    justify-content: center;
  }

  .footer-bottom {
    flex-direction: column;
    gap: 8px;
    text-align: center;
  }
}

/* Small tablets & large phones (481px - 768px) */
@media (max-width: 768px) {
  .header {
    padding: 12px 20px;
  }

  .logo {
    font-size: 24px;
  }

  .rocket-img {
    width: 90px;
  }

  .main-title {
    font-size: 26px;
  }

  .subtitle {
    font-size: 15px;
  }

  .footer {
    padding: 20px;
  }

  .footer-links {
    gap: 20px;
    flex-wrap: wrap;
  }

  .small-links {
    gap: 10px;
    justify-content: center;
  }
}

/* Tablets & small laptops (769px - 1024px) */
@media (max-width: 1024px) {
  .header {
    padding: 15px 25px;
  }

  .logo {
    font-size: 26px;
  }

  .rocket-img {
    width: 100px;
  }

  .main-title {
    font-size: 28px;
  }

  .footer {
    padding: 20px 40px;
  }

  .footer-links {
    gap: 25px;
  }
}

/* Large screens (above 1440px) â€” optional upscale */
@media (min-width: 1440px) {
  .logo {
    font-size: 32px;
  }

  .rocket-img {
    width: 140px;
  }

  .main-title {
    font-size: 36px;
  }

  .subtitle {
    font-size: 18px;
  }

  .footer {
    padding: 30px 200px;
  }
}
/* ========== EXTRA RESPONSIVE DESIGN ========== */

/* Very small devices (max 360px) */
@media (max-width: 360px) {
  .main-title {
    font-size: 20px;
  }
  .subtitle {
    font-size: 13px;
  }
  .menu-overlay {
    top: 2%;
    left: 4%;
    width: 92%;
    padding: 10px 20px;
  }
}

/* Large tablets (1025px - 1366px) */
@media (min-width: 1025px) and (max-width: 1366px) {
  .header {
    padding: 18px 40px;
  }

  .main-title {
    font-size: 30px;
  }

  .footer {
    padding: 20px 80px;
  }

  .footer-links {
    gap: 35px;
  }
}

/* Ultra wide screens (min 1800px) */
@media (min-width: 1800px) {
  .main-title {
    font-size: 42px;
  }
  .subtitle {
    font-size: 20px;
  }
  .rocket-img {
    width: 180px;
  }
  .footer {
    padding: 40px 300px;
    font-size: 16px;
  }
}
